@import ../App/app

.upload__container
  display: flex
  flex-direction: column

.upload__title
  margin: 0 0 1.5rem 0

.upload__form
  display: flex
  justify-content: space-between
  align-items: center
  padding: $padding
  border-radius: $borderRadius
  background-color: $backgroundComponentsColor

#file-upload-button
  background-color: black

.upload__view
  margin: 1rem 0
  text-align: center

.avatar__preview
  width: 90%
  height: 90%
  box-shadow: 1px 0px 5px #3d3d3d

.upload__avatar__preview
  display: flex
  flex-direction: row
  align-items: center
  padding: $padding
  background-color: $backgroundComponentsColor
  margin: 1.5rem 0
  border-radius: $borderRadius

@media screen and ( max-width: 700px )
  .upload__avatar__preview
    flex-direction: column
    background-color: transparent
    div
      margin: 0 0 .5rem 0
    :nth-child(+n+2)
      display: none
  .upload__view
    display: none
