@import ../App/app

.login__form
  display: flex
  flex-direction: row

.title__box
  display: flex
  flex-direction: row
  max-width: 400px
  margin: 0 1rem 0 0

.form__box
  display: flex
  position: relative
  flex-direction: column
  margin: 5rem auto
  padding: 20px
  width: 400px
  border-radius: .8rem
  background-color: $backgroundComponentsColor
  box-shadow: 0px 1px 6px #3d3d3d

.login__form a
  color: $primaryColor
  font-weight: 500
  transition: all ease 0.1s

.login__form a:hover
  transform: scale(1.1)

.form__h2
  text-align: center

.form__linkBox
  display: flex
  flex-direction: row
  justify-content: space-around

@media screen and ( max-width: 1100px )
  .login__form
    flex-direction: column
  .form__box
    margin: 1rem auto
  