@import ../App/app

.settings__form
  display: flex
  flex-direction: column
  font-weight: 500
  font-size: 18px
  width: 100%
  height: 100%
  border-radius: 0 0 .3rem .3rem
  background-color: $backgroundComponentsColor
  padding: $padding

.settings__btnForm
  display: flex
  flex-direction: row
  font-weight: 500
  font-size: 18px
  border-radius: .3rem .3rem 0 0
  background-color: $backgroundComponentsColor
  padding: $padding

.settings__text
  text-align: center
  margin: 1rem
  padding: $padding
  box-shadow: 0px 1px 4px #3d3d3d
  border-radius: 0.3rem

.settings__data
  display: flex
  flex-direction: row
  justify-content: space-around

.settings__mainData
  display: flex
  width: 100%
  padding: 1rem 2rem
  flex-direction: column

.settings__otherData
  display: flex
  width: 100%
  padding: 1rem 2rem
  flex-direction: column

.settings__notification
  text-align: center
  color: $successMessageColor

.settings__btn
  text-transform: uppercase
  padding: 0.5rem
  border-radius: 0.3rem
  color: $backgroundComponentsColor
  margin: 1rem
  background-color: $primaryColor
  font-size: 14px
  font-weight: 400
  transition: all ease .2s

.settings__btn:hover
  background-color: $secondaryColor

@media screen and ( max-width: 765px )
  .settings__container
    margin: 1rem

@media screen and ( max-width: 500px )
  .settings__data
    flex-direction: column

@media screen and ( max-width: 380px )
  .settings__form
    padding: 0
  .settings__btnForm
    padding: 0
    a
      margin: .2rem .2rem
